        <!-- „Éó„É≠„Éï„Ç£„Éº„É´Á∑®ÈõÜ„Éï„Ç©„Éº„É† -->
        <form @submit.prevent="handleUpdate">
          <div class="form-group">
            <label for="username">„É¶„Éº„Ç∂„ÉºÂêç</label>
            <input
              type="text"
              id="username"
              v-model="username"
              placeholder="„É¶„Éº„Ç∂„ÉºÂêç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
              required
            />
          </div>
          
          <div class="form-group">
            <label for="email">„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ</label>
            <input
              type="email"
              id="email"
              v-model="email"
              placeholder="„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
              required
            />
          </div>
          
          <div class="form-group">
            <label for="age">Âπ¥ÈΩ¢</label>
            <input
              type="number"
              id="age"
              v-model="age"
              placeholder="Âπ¥ÈΩ¢„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
              min="0"
            />
          </div>
          
          <div class="form-group">
            <label for="gender">ÊÄßÂà•</label>
            <select id="gender" v-model="gender" required>
              <option disabled value="">ÈÅ∏Êäû„Åó„Å¶„Åè„Å†„Åï„ÅÑ</option>
              <option value="Áî∑ÊÄß">Áî∑ÊÄß</option>
              <option value="Â•≥ÊÄß">Â•≥ÊÄß</option>
              <option value="„Åù„ÅÆ‰ªñ">„Åù„ÅÆ‰ªñ</option>
            </select>
          </div>
          
          <!-- Êõ¥Êñ∞„Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏ -->
          <div v-if="updateErrorMessage" class="error-message">
            {{ updateErrorMessage }}
          </div>
          
          <!-- „Éó„É≠„Éï„Ç£„Éº„É´Êõ¥Êñ∞„Éú„Çø„É≥ -->
          <button type="submit" class="btn btn-primary" :disabled="isUpdating">
            „Éó„É≠„Éï„Ç£„Éº„É´Â§âÊõ¥
          </button>
          
          <!-- „Éó„É≠„Éï„Ç£„Éº„É´Êõ¥Êñ∞‰∏≠„ÅÆ„É≠„Éº„Éá„Ç£„É≥„Ç∞„Ç§„É≥„Ç∏„Ç±„Éº„Çø„Éº -->
          <div v-if="isUpdating" class="loading-spinner">
            Êõ¥Êñ∞‰∏≠...
          </div>
        </form>
        
        <!-- „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥„É™„É≥„ÇØÔºà„Éõ„Éº„É†„ÄÅ„ÅäÊ∞ó„Å´ÂÖ•„ÇäÔºâ -->
        <div class="navigation-links">
          <router-link to="/home" class="nav-link">
            <button class="btn btn-secondary">„Éõ„Éº„É†„Å∏Êàª„Çã</button>
          </router-link>
          
          <router-link to="/favorites" class="nav-link">
            <button class="btn btn-secondary">„ÅäÊ∞ó„Å´ÂÖ•„Çä‰∏ÄË¶ß</button>
          </router-link>
        </div>
        
        <!-- „Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥„Å∏„ÅÆ„É™„É≥„ÇØ„Éú„Çø„É≥ -->
        <div class="password-change-section">
          <router-link to="/change-password" class="nav-link">
            <button class="btn btn-secondary">„Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥</button>
          </router-link>
        </div>
        
        <!-- ÈÄÄ‰ºö„Éú„Çø„É≥ -->
        <div class="account-deletion-section">
          <h3>„Ç¢„Ç´„Ç¶„É≥„ÉàÂâäÈô§</h3>
          <button @click="confirmDeleteAccount" class="btn btn-danger">
            ÈÄÄ‰ºö
          </button>
          
          <!-- ÈÄÄ‰ºöÁ¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞ -->
          <div v-if="showDeleteConfirmation" class="confirmation-dialog">
            <p>Êú¨ÂΩì„Å´„Ç¢„Ç´„Ç¶„É≥„Éà„ÇíÂâäÈô§„Åó„Åæ„Åô„ÅãÔºü„Åì„ÅÆÊìç‰Ωú„ÅØÂèñ„ÇäÊ∂à„Åõ„Åæ„Åõ„Çì„ÄÇ</p>
            <button @click="handleDeleteAccount" class="btn btn-danger confirm-button">„ÅØ„ÅÑ„ÄÅÂâäÈô§„Åó„Åæ„Åô</button>
            <button @click="cancelDeleteAccount" class="btn btn-secondary cancel-button">„Ç≠„É£„É≥„Çª„É´</button>
          </div>
          
          <!-- ÈÄÄ‰ºö„Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏ -->
          <div v-if="deleteErrorMessage" class="error-message">
            {{ deleteErrorMessage }}
          </div>
        </div>
        
        <!-- ÊàêÂäü„É°„ÉÉ„Çª„Éº„Ç∏ -->
        <div v-if="successMessage" class="success-message">
          {{ successMessage }}
        </div>
        
      </div>
    </div>
  </template>
  
  <script>
  import axios from '@/axios';
  import { ref, onMounted } from 'vue';
  import { useRouter } from 'vue-router';
  
  export default {
    name: 'MyPage',
    setup() {
      const router = useRouter();
      
      // „Éó„É≠„Éï„Ç£„Éº„É´Á∑®ÈõÜÁî®„ÅÆ„Éá„Éº„Çø
      const username = ref('');
      const email = ref('');
      const age = ref('');
      const gender = ref('');
      const isLoading = ref(true);
      const isUpdating = ref(false);
      const errorMessage = ref('');
      const updateErrorMessage = ref('');
      const successMessage = ref('');
  
      // ÈÄÄ‰ºöÁî®„ÅÆ„Éá„Éº„Çø
      const showDeleteConfirmation = ref(false);
      const deleteErrorMessage = ref('');
  
      /**
       * „É¶„Éº„Ç∂„Éº„Éá„Éº„Çø„ÇíÂèñÂæó„Åô„ÇãÈñ¢Êï∞
       */
      const fetchUserData = async () => {
        try {
          const response = await axios.get('/getUserData', {
            withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
          });
  
          if (response.data.status === 'success') {
            const user = response.data.user;
            username.value = user.name || ''; // user.name „Çí username „Å´„Éû„ÉÉ„Éî„É≥„Ç∞
            email.value = user.email || '';
            age.value = user.age !== null && user.age !== undefined ? user.age : '';
            gender.value = user.gender || '';
            // ‰ªñ„ÅÆ„Éï„Ç£„Éº„É´„ÉâÔºàstress, dreamThemeÔºâ„ÇÇÂøÖË¶Å„Å´Âøú„Åò„Å¶Ë®≠ÂÆö
          } else {
            errorMessage.value = response.data.message || '„É¶„Éº„Ç∂„Éº„Éá„Éº„Çø„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('„É¶„Éº„Ç∂„Éº„Éá„Éº„ÇøÂèñÂæó„Ç®„É©„Éº:', error);
          errorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          isLoading.value = false;
        }
      };
  
      /**
       * „Éó„É≠„Éï„Ç£„Éº„É´„ÇíÊõ¥Êñ∞„Åô„ÇãÈñ¢Êï∞
       */
      const handleUpdate = async () => {
        updateErrorMessage.value = '';
        successMessage.value = '';
        isUpdating.value = true;
  
        // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥ÔºàÁ∞°ÊòìÔºâ
        if (username.value.trim() === '') {
          updateErrorMessage.value = '„É¶„Éº„Ç∂„ÉºÂêç„ÅØÂøÖÈ†à„Åß„Åô„ÄÇ';
          isUpdating.value = false;
          return;
        }
        if (email.value.trim() === '') {
          updateErrorMessage.value = '„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅØÂøÖÈ†à„Åß„Åô„ÄÇ';
          isUpdating.value = false;
          return;
        }
        // „É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÅÆÂΩ¢Âºè„ÉÅ„Çß„ÉÉ„ÇØÔºàÁ∞°ÊòìÔºâ
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        if (!emailRegex.test(email.value.trim())) {
          updateErrorMessage.value = 'ÊúâÂäπ„Å™„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ';
          isUpdating.value = false;
          return;
        }
  
        try {
          const response = await axios.post(
            '/updateUser',
            {
              username: username.value.trim(),
              email: email.value.trim(),
              age: age.value ? parseInt(age.value) : null,
              gender: gender.value,
            },
            {
              headers: {
                'Content-Type': 'application/json',
              },
              withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
            }
          );
  
          if (response.data.status === 'success') {
            successMessage.value = '„Éó„É≠„Éï„Ç£„Éº„É´„ÅåÊõ¥Êñ∞„Åï„Çå„Åæ„Åó„Åü„ÄÇ';
            // ÂøÖË¶Å„Å´Âøú„Åò„Å¶„ÄÅÂÜçÂ∫¶„É¶„Éº„Ç∂„Éº„Éá„Éº„Çø„ÇíÂèñÂæó„Åô„Çã
            await fetchUserData();
          } else {
            updateErrorMessage.value = response.data.message || '„Éó„É≠„Éï„Ç£„Éº„É´„ÅÆÊõ¥Êñ∞„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('„Éó„É≠„Éï„Ç£„Éº„É´Êõ¥Êñ∞„Ç®„É©„Éº:', error);
          updateErrorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          isUpdating.value = false;
        }
      };
  
      /**
       * ÈÄÄ‰ºöÁ¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞„ÇíË°®Á§∫„Åô„ÇãÈñ¢Êï∞
       */
      const confirmDeleteAccount = () => {
        showDeleteConfirmation.value = true;
      };
  
      /**
       * ÈÄÄ‰ºö„ÇíÂá¶ÁêÜ„Åô„ÇãÈñ¢Êï∞
       */
      const handleDeleteAccount = async () => {
        deleteErrorMessage.value = '';
        
        try {
          const response = await axios.delete('/deleteAccount', {
            withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
          });
  
          if (response.data.status === 'success') {
            alert('„Ç¢„Ç´„Ç¶„É≥„Éà„ÅåÂâäÈô§„Åï„Çå„Åæ„Åó„Åü„ÄÇ„ÅîÂà©Áî®„ÅÇ„Çä„Åå„Å®„ÅÜ„Åî„Åñ„ÅÑ„Åæ„Åó„Åü„ÄÇ');
            router.push('/'); // „Éõ„Éº„É†„Éö„Éº„Ç∏„Å´„É™„ÉÄ„Ç§„É¨„ÇØ„Éà
          } else {
            deleteErrorMessage.value = response.data.message || '„Ç¢„Ç´„Ç¶„É≥„Éà„ÅÆÂâäÈô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('„Ç¢„Ç´„Ç¶„É≥„ÉàÂâäÈô§„Ç®„É©„Éº:', error);
          deleteErrorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          showDeleteConfirmation.value = false;
        }
      };
  
      /**
       * ÈÄÄ‰ºöÁ¢∫Ë™ç„ÉÄ„Ç§„Ç¢„É≠„Ç∞„Çí„Ç≠„É£„É≥„Çª„É´„Åô„ÇãÈñ¢Êï∞
       */
      const cancelDeleteAccount = () => {
        showDeleteConfirmation.value = false;
      };
  
      // „Ç≥„É≥„Éù„Éº„Éç„É≥„Éà„ÅÆ„Éû„Ç¶„É≥„ÉàÊôÇ„Å´„É¶„Éº„Ç∂„Éº„Éá„Éº„Çø„ÇíÂèñÂæó
      onMounted(() => {
        fetchUserData();
      });
  
      return {
        // „Éó„É≠„Éï„Ç£„Éº„É´Á∑®ÈõÜ
        username,
        email,
        age,
        gender,
        isLoading,
        isUpdating,
        errorMessage,
        updateErrorMessage,
        successMessage,
        handleUpdate,
  
        // ÈÄÄ‰ºö
        showDeleteConfirmation,
        deleteErrorMessage,
        confirmDeleteAccount,
        handleDeleteAccount,
        cancelDeleteAccount,
      };
    },
  };
  </script>
  
