<template>
    <div class="login-container">
      <h2>„É≠„Ç∞„Ç§„É≥</h2>
      <form @submit.prevent="handleLogin">
        <div class="form-group">
          <label for="username">„É¶„Éº„Ç∂„ÉºÂêç</label>
          <input
            type="text"
            id="username"
            v-model="username"
            required
            placeholder="„É¶„Éº„Ç∂„ÉºÂêç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div class="form-group">
          <label for="password">„Éë„Çπ„ÉØ„Éº„Éâ</label>
          <input
            type="password"
            id="password"
            v-model="password"
            required
            placeholder="„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div v-if="errorMessage" class="error-message">
          {{ errorMessage }}
        </div>
        <button type="submit" :disabled="isLoading">
          „É≠„Ç∞„Ç§„É≥
        </button>
        <div v-if="isLoading" class="loading-spinner">
          „É≠„Ç∞„Ç§„É≥‰∏≠...
        </div>
      </form>
      <p>
        „Ç¢„Ç´„Ç¶„É≥„Éà„Çí„ÅäÊåÅ„Å°„Åß„Å™„ÅÑ„Åß„Åô„ÅãÔºü
        <router-link to="/register">‰ºöÂì°ÁôªÈå≤„ÅØ„Åì„Å°„Çâ</router-link>
      </p>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import { ref } from 'vue';
  import { useRouter } from 'vue-router';
  
  export default {
    name: 'Login',
    setup() {
      const username = ref('');
      const password = ref('');
      const errorMessage = ref('');
      const isLoading = ref(false);
      const router = useRouter();
  
      const handleLogin = async () => {
        errorMessage.value = '';
        isLoading.value = true;
  
        try {
          const response = await axios.post(
            'http://localhost:3000/api/login',
            {
              username: username.value,
              password: password.value,
            },
            {
              headers: {
                'Content-Type': 'application/json',
              },
              withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
            }
          );
  
          if (response.data.status === 'success') {
            // „É≠„Ç∞„Ç§„É≥ÊàêÂäü
            alert('„É≠„Ç∞„Ç§„É≥„Å´ÊàêÂäü„Åó„Åæ„Åó„Åü„ÄÇ');
            router.push('/'); // „Éõ„Éº„É†„Éö„Éº„Ç∏„Å´„É™„ÉÄ„Ç§„É¨„ÇØ„Éà
          } else {
            // „É≠„Ç∞„Ç§„É≥Â§±Êïó
            errorMessage.value = response.data.message || '„É≠„Ç∞„Ç§„É≥„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('„É≠„Ç∞„Ç§„É≥„Ç®„É©„Éº:', error);
          errorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          isLoading.value = false;
        }
      };
  
      return {
        username,
        password,
        handleLogin,
        errorMessage,
        isLoading,
      };
    },
  };
  </script>
  
  <style scoped>
  .login-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #fafafa;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .form-group {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  input[type='text'],
  input[type='password'] {
    width: 100%;
    padding: 10px;
    border: 1px solid #bbb;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button:disabled {
    background-color: #a0c4ff;
    cursor: not-allowed;
  }
  
  .error-message {
    color: red;
    margin-bottom: 10px;
    text-align: center;
  }
  
  .loading-spinner {
    text-align: center;
    margin-top: 10px;
    color: #555;
  }
  </style>
  