<template>
    <div class="password-change-container">
      <h2>„Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥</h2>
      <form @submit.prevent="handleChangePassword">
        
        <!-- 1. ÁèæÂú®„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ„Éï„Ç£„Éº„É´„Éâ„ÇíÂÜçËøΩÂä† -->
        <div class="form-group">
          <label for="currentPassword">ÁèæÂú®„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ</label>
          <input
            type="password"
            id="currentPassword"
            v-model="currentPassword"
            placeholder="ÁèæÂú®„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
            required
          />
        </div>
  
        <!-- 2. Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„Éï„Ç£„Éº„É´„Éâ -->
        <div class="form-group">
          <label for="newPassword">Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ</label>
          <input
            type="password"
            id="newPassword"
            v-model="newPassword"
            placeholder="Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
            required
          />
        </div>
        
        <!-- 3. Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç„Éï„Ç£„Éº„É´„Éâ -->
        <div class="form-group">
          <label for="confirmNewPassword">Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç</label>
          <input
            type="password"
            id="confirmNewPassword"
            v-model="confirmNewPassword"
            placeholder="Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÜçÂ∫¶ÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
            required
          />
        </div>
        
        <!-- 4. „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏ -->
        <div v-if="passwordErrorMessage" class="error-message">
          {{ passwordErrorMessage }}
        </div>
        
        <!-- 5. ÊàêÂäü„É°„ÉÉ„Çª„Éº„Ç∏ -->
        <div v-if="passwordSuccessMessage" class="success-message">
          {{ passwordSuccessMessage }}
        </div>
        
        <!-- 6. ÈÄÅ‰ø°„Éú„Çø„É≥ -->
        <button type="submit" :disabled="isChangingPassword">
          „Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥
        </button>
        
        <!-- 7. „É≠„Éº„Éá„Ç£„É≥„Ç∞„Ç§„É≥„Ç∏„Ç±„Éº„Çø„Éº -->
        <div v-if="isChangingPassword" class="loading-spinner">
          Â§âÊõ¥‰∏≠...
        </div>
      </form>
    </div>
  </template>
  
  <script>
  import axios from '@/axios';
  import { ref } from 'vue';
  import { useRouter } from 'vue-router';
  
  export default {
    name: 'PasswordChange',
    setup() {
      const router = useRouter();
      
      // 1. ÁèæÂú®„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ„ÅÆ„Éá„Éº„Çø„Éê„Ç§„É≥„Éá„Ç£„É≥„Ç∞
      const currentPassword = ref('');
      
      // 2. Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„ÅÆ„Éá„Éº„Çø„Éê„Ç§„É≥„Éá„Ç£„É≥„Ç∞
      const newPassword = ref('');
      
      // 3. Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç„ÅÆ„Éá„Éº„Çø„Éê„Ç§„É≥„Éá„Ç£„É≥„Ç∞
      const confirmNewPassword = ref('');
      
      // 4. „É≠„Éº„Éá„Ç£„É≥„Ç∞Áä∂ÊÖã„ÅÆÁÆ°ÁêÜ
      const isChangingPassword = ref(false);
      
      // 5. „Ç®„É©„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆÁÆ°ÁêÜ
      const passwordErrorMessage = ref('');
      
      // 6. ÊàêÂäü„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆÁÆ°ÁêÜ
      const passwordSuccessMessage = ref('');
  
      // 7. „Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥„ÇíÂá¶ÁêÜ„Åô„ÇãÈñ¢Êï∞
      const handleChangePassword = async () => {
        // „É°„ÉÉ„Çª„Éº„Ç∏„ÅÆÂàùÊúüÂåñ
        passwordErrorMessage.value = '';
        passwordSuccessMessage.value = '';
  
        // „Éê„É™„Éá„Éº„Ç∑„Éß„É≥: Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„Å®Á¢∫Ë™ç„Éë„Çπ„ÉØ„Éº„Éâ„Åå‰∏ÄËá¥„Åô„Çã„Åì„Å®
        if (newPassword.value !== confirmNewPassword.value) {
          passwordErrorMessage.value = 'Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ„Åå‰∏ÄËá¥„Åó„Åæ„Åõ„Çì„ÄÇ';
          return;
        }
  
        // „É≠„Éº„Éá„Ç£„É≥„Ç∞Áä∂ÊÖã„ÇíÈñãÂßã
        isChangingPassword.value = true;
  
        try {
          // 8. „Éê„ÉÉ„ÇØ„Ç®„É≥„Éâ„Å´„É™„ÇØ„Ç®„Çπ„Éà„ÇíÈÄÅ‰ø°
          const response = await axios.post(
            '/changePassword',
            {
              currentPassword: currentPassword.value, // ÁèæÂú®„ÅÆ„Éë„Çπ„ÉØ„Éº„Éâ
              newPassword: newPassword.value,         // Êñ∞„Åó„ÅÑ„Éë„Çπ„ÉØ„Éº„Éâ
            },
            {
              headers: {
                'Content-Type': 'application/json',
              },
              withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
            }
          );
  
          // 9. „É¨„Çπ„Éù„É≥„Çπ„ÅÆÂá¶ÁêÜ
          if (response.data.status === 'success') {
            passwordSuccessMessage.value = '„Éë„Çπ„ÉØ„Éº„Éâ„ÅåÂ§âÊõ¥„Åï„Çå„Åæ„Åó„Åü„ÄÇ';
            // „Éï„Ç©„Éº„É†„ÅÆ„É™„Çª„ÉÉ„Éà
            currentPassword.value = '';
            newPassword.value = '';
            confirmNewPassword.value = '';
            // „Éû„Ç§„Éö„Éº„Ç∏„Å´„É™„ÉÄ„Ç§„É¨„ÇØ„Éà
            router.push('/mypage');
          } else {
            passwordErrorMessage.value = response.data.message || '„Éë„Çπ„ÉØ„Éº„Éâ„ÅÆÂ§âÊõ¥„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('„Éë„Çπ„ÉØ„Éº„ÉâÂ§âÊõ¥„Ç®„É©„Éº:', error);
          passwordErrorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          // „É≠„Éº„Éá„Ç£„É≥„Ç∞Áä∂ÊÖã„ÇíÁµÇ‰∫Ü
          isChangingPassword.value = false;
        }
      };
  
      return {
        currentPassword,
        newPassword,
        confirmNewPassword,
        isChangingPassword,
        passwordErrorMessage,
        passwordSuccessMessage,
        handleChangePassword,
      };
    },
  };
  </script>
  
  <style scoped>
