// src/main.js
import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import axios from './axios'; // „Ç´„Çπ„Çø„É†Axios„Ç§„É≥„Çπ„Çø„É≥„Çπ„Çí„Ç§„É≥„Éù„Éº„Éà
import Toast, { POSITION } from 'vue-toastification';
import 'vue-toastification/dist/index.css'; // „Çπ„Çø„Ç§„É´„Ç∑„Éº„Éà„ÅÆ„Ç§„É≥„Éù„Éº„Éà

const app = createApp(App);

// „Ç∞„É≠„Éº„Éê„É´„Éó„É≠„Éë„ÉÜ„Ç£„Å®„Åó„Å¶Axios„ÇíË®≠ÂÆöÔºà„Ç™„Éó„Ç∑„Éß„É≥Ôºâ
app.config.globalProperties.$axios = axios;

app.use(router);
app.use(Toast, {
  // „Ç™„Éó„Ç∑„Éß„É≥„ÇíÂøÖË¶Å„Å´Âøú„Åò„Å¶Ë®≠ÂÆö
  position: POSITION.TOP_RIGHT,
  timeout: 5000,
  closeOnClick: true,
  pauseOnFocusLoss: true,
  pauseOnHover: true,
});

app.mount('#app');
