<template>
  <div class="home">
    <!-- „Éò„ÉÉ„ÉÄ„Éº -->
    <header>
      <div class="header-title-container">
        <h1 class="header-title">„É™„Ç¢„É´„Çø„Ç§„É†Â§¢Ëß£Êûê„ÉÅ„É£„ÉÉ„Éà„Éú„ÉÉ„Éà</h1>
      </div>
      <div class="auth-buttons">
        <button v-if="!isLoggedIn" @click="goToLogin">„É≠„Ç∞„Ç§„É≥</button>
        <button v-if="!isLoggedIn" @click="goToRegister">‰ºöÂì°ÁôªÈå≤</button>
        <button v-if="isLoggedIn" @click="logout">„É≠„Ç∞„Ç¢„Ç¶„Éà</button>
        <button v-if="isLoggedIn" @click="goToMyPage">„Éû„Ç§„Éö„Éº„Ç∏</button>
        <!-- „Äå„ÅäÊ∞ó„Å´ÂÖ•„Çä‰∏ÄË¶ß„Äç„Éú„Çø„É≥„ÇíËøΩÂä†„Åô„ÇãÂ†¥Âêà„ÅØ‰ª•‰∏ã„ÅÆ„Ç≥„É°„É≥„Éà„ÇíÂ§ñ„Åó„Å¶„Åè„Å†„Åï„ÅÑ -->
        <!-- <button v-if="isLoggedIn" @click="goToFavorites">„ÅäÊ∞ó„Å´ÂÖ•„Çä‰∏ÄË¶ß</button> -->
      </div>
    </header>

    <!-- „ÉÅ„É£„ÉÉ„Éà„Çª„ÇØ„Ç∑„Éß„É≥ -->
    <div class="chat-container">
      <!-- AIÂá¶ÁêÜ‰∏≠„ÅÆ„Ç§„É≥„Ç∏„Ç±„Éº„Çø„Éº -->
      <div v-if="isLoading" class="loading-indicator">
        <div class="spinner"></div>
        <span>AIÂ§¢Âç†„ÅÑ‰∏≠„Éª„Éª„Éª</span>
      </div>
      <div class="chat-history" ref="chatHistoryDiv">
        <div
          v-for="msg in chatHistory"
          :key="msg.id"
          :class="['chat-message', msg.type]"
        >
          <!-- „Éú„ÉÉ„Éà„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„É¨„É≥„ÉÄ„É™„É≥„Ç∞ -->
          <div v-if="msg.type === 'bot'" v-html="escapeHTML(msg.text)"></div>
          
          <!-- „Éú„ÉÉ„ÉàÂà∂Èôê„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„É¨„É≥„ÉÄ„É™„É≥„Ç∞ -->
          <div v-else-if="msg.type === 'bot-restriction'" v-html="msg.html"></div>
          
          <!-- „É¶„Éº„Ç∂„Éº„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„É¨„É≥„ÉÄ„É™„É≥„Ç∞ -->
          <div v-else>{{ msg.text }}</div>

          <!-- „ÅäÊ∞ó„Å´ÂÖ•„Çä„Éú„Çø„É≥Ôºà„Éú„ÉÉ„Éà„É°„ÉÉ„Çª„Éº„Ç∏„ÅÆ„ÅøË°®Á§∫„ÄÅ„É≠„Ç∞„Ç§„É≥ÊôÇ„ÅÆ„ÅøÔºâ -->
          <button
            v-if="msg.type === 'bot' && isLoggedIn"
            @click="toggleFavorite(msg.id)"
            :class="['favorite-button', msg.isFavorite ? 'favorited' : '']"
            :aria-label="msg.isFavorite ? '„ÅäÊ∞ó„Å´ÂÖ•„Çä„ÇíËß£Èô§„Åô„Çã' : '„ÅäÊ∞ó„Å´ÂÖ•„Çä„Å´ËøΩÂä†„Åô„Çã'"
          >
            <!-- „ÅäÊ∞ó„Å´ÂÖ•„Çä„Ç¢„Ç§„Ç≥„É≥Ôºà„Éè„Éº„Éà„Éû„Éº„ÇØÔºâ -->
            <span v-if="msg.isFavorite">‚ù§Ô∏è</span>
            <span v-else>ü§ç</span>
          </button>
        </div>
      </div>
      <div class="chat-input">
        <input
          type="text"
          v-model="message"
          @keyup.enter="sendMessage"
          placeholder="„ÅÇ„Å™„Åü„ÅÆÂ§¢„ÇíÊïô„Åà„Å¶„Åè„Å†„Åï„ÅÑ..."
          :disabled="isLoading"
        />
        <button @click="sendMessage" :disabled="isLoading">ÈÄÅ‰ø°</button>
        <button @click="clearMessages" class="clear-button">„É°„ÉÉ„Çª„Éº„Ç∏„ÇØ„É™„Ç¢</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/axios'; // Ê®ôÊ∫ñ„ÅÆaxios„Çí‰ΩøÁî®
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { useToast } from 'vue-toastification';

export default {
  name: 'Home',
  setup() {
    const message = ref('');
    const chatHistory = ref([]);
    const isLoggedIn = ref(false);
    const isLoading = ref(false);
    const chatHistoryDiv = ref(null);
    const router = useRouter();
    const toast = useToast();

    /**
     * „ÉÜ„Ç≠„Çπ„Éà„Çí„Ç®„Çπ„Ç±„Éº„Éó„Åô„ÇãÈñ¢Êï∞ÔºàXSSÂØæÁ≠ñÔºâ
     */
    const escapeHTML = (str) =>
      str.replace(/&/g, '&amp;').replace(/</g, '&lt;').replace(/>/g, '&gt;');

// Áõ∏ÂØæ„Éë„Çπ„Å´Â§âÊõ¥„Åó„Åü‰æã

/**
 * „É≠„Ç∞„Ç§„É≥Áä∂ÊÖã„ÇíÁ¢∫Ë™ç„Åô„ÇãÈñ¢Êï∞
 */
 const checkLoginStatus = async () => {
  try {
    const response = await axios.get('/api/checksession', { withCredentials: true });
    isLoggedIn.value = response.data.loggedIn;
    if (isLoggedIn.value) {
      await fetchFavorites();
    }
  } catch (error) {
    console.error('„É≠„Ç∞„Ç§„É≥Áä∂ÊÖãÁ¢∫Ë™ç„Ç®„É©„Éº:', error);
  }
};

/**
 * „É¶„Éº„Ç∂„Éº„ÅÆ„ÅäÊ∞ó„Å´ÂÖ•„Çä„Ç¢„Ç§„ÉÜ„É†„ÇíÂèñÂæó„Åô„ÇãÈñ¢Êï∞
 */
const fetchFavorites = async () => {
  try {
    const response = await axios.get('/api/favorites', { withCredentials: true });
    if (response.data.status === 'success') {
      const favoriteIds = response.data.favorites.map(fav => fav.message_id);
      // „ÉÅ„É£„ÉÉ„ÉàÂ±•Ê≠¥„ÅÆÂêÑ„É°„ÉÉ„Çª„Éº„Ç∏„Å´„ÅäÊ∞ó„Å´ÂÖ•„ÇäÁä∂ÊÖã„ÇíË®≠ÂÆö
      chatHistory.value.forEach(msg => {
        if (msg.type === 'bot') {
          msg.isFavorite = favoriteIds.includes(msg.id);
        }
      });
    } else {
      toast.error(response.data.message || '„ÅäÊ∞ó„Å´ÂÖ•„Çä„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
    }
  } catch (error) {
    console.error('„ÅäÊ∞ó„Å´ÂÖ•„ÇäÂèñÂæó„Ç®„É©„Éº:', error);
    toast.error('„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ');
  }
};

    /**
     * „ÅäÊ∞ó„Å´ÂÖ•„Çä„ÇíËøΩÂä†„ÉªËß£Èô§„Åô„ÇãÈñ¢Êï∞
     * @param {Number} messageId 
     */
    const toggleFavorite = async (messageId) => {
      if (!isLoggedIn.value) {
        toast.info('„ÅäÊ∞ó„Å´ÂÖ•„ÇäÊ©üËÉΩ„Çí‰ΩøÁî®„Åô„Çã„Å´„ÅØ„É≠„Ç∞„Ç§„É≥„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ');
        return;
      }

      const messageItem = chatHistory.value.find(msg => msg.id === messageId);
      if (!messageItem) return;

      try {
        if (messageItem.isFavorite) {
          // „ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§
          const response = await axios.delete(`/favorites/${messageId}`, { withCredentials: true });
          if (response.data.status === 'success') {
            messageItem.isFavorite = false;
            toast.success('„ÅäÊ∞ó„Å´ÂÖ•„Çä„Åã„ÇâËß£Èô§„Åó„Åæ„Åó„Åü„ÄÇ');
          } else {
            toast.error(response.data.message || '„ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
          }
        } else {
          // „ÅäÊ∞ó„Å´ÂÖ•„ÇäËøΩÂä†
          const response = await axios.post('/api/favorites', { messageId }, { withCredentials: true });
          if (response.data.status === 'success') {
            messageItem.isFavorite = true;
            toast.success('„ÅäÊ∞ó„Å´ÂÖ•„Çä„Å´ËøΩÂä†„Åó„Åæ„Åó„Åü„ÄÇ');
          } else {
            toast.error(response.data.message || '„ÅäÊ∞ó„Å´ÂÖ•„ÇäËøΩÂä†„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
          }
        }
      } catch (error) {
        console.error('„ÅäÊ∞ó„Å´ÂÖ•„ÇäÊìç‰Ωú„Ç®„É©„Éº:', error);
        toast.error(error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ');
      }
    };

    /**
     * „É°„ÉÉ„Çª„Éº„Ç∏„ÇíÈÄÅ‰ø°„Åô„ÇãÈñ¢Êï∞
     */
    const sendMessage = async () => {
      if (!message.value.trim()) return;
      const userMessage = message.value.trim();
      const userMessageId = Date.now(); // „É¶„Éº„Ç∂„Éº„ÅÆ„É°„ÉÉ„Çª„Éº„Ç∏IDÔºà„ÇØ„É©„Ç§„Ç¢„É≥„ÉàÁîüÊàêÔºâ
      chatHistory.value.push({ id: userMessageId, text: userMessage, type: 'user', isFavorite: false });
      message.value = '';
      isLoading.value = true;

      try {
        const response = await axios.post(
          '/interpret-dream',
          { dream: userMessage },
          {
            headers: {
              'Content-Type': 'application/json',
            },
            withCredentials: true
          }
        );

        if (response.data.success) {
          const { interpretation, interactionId } = response.data; // interactionId„ÇíÂèñÂæó
          chatHistory.value.push({ id: interactionId, text: interpretation, type: 'bot', isFavorite: false });
          // „ÅäÊ∞ó„Å´ÂÖ•„ÇäÁä∂ÊÖã„ÇíÂèçÊò†
          if (isLoggedIn.value) {
            await fetchFavorites();
          }
        } else if (response.data.message === 'Á∂ö„Åç„ÅØ‰ºöÂì°ÁôªÈå≤„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ') {
          const restrictionMessage = 'Á∂ö„Åç„ÅØ<a href="/register">‰ºöÂì°ÁôªÈå≤</a>„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ';
          chatHistory.value.push({ id: Date.now() + 2, html: restrictionMessage, type: 'bot-restriction', isFavorite: false });
          toast.info('Á∂ö„Åç„ÅÆ‰ºöË©±„Å´„ÅØ‰ºöÂì°ÁôªÈå≤„ÅåÂøÖË¶Å„Åß„Åô„ÄÇ‰ºöÂì°ÁôªÈå≤„Éö„Éº„Ç∏„Å´ÁßªÂãï„Åó„Åæ„Åô„ÄÇ');
          router.push('/register');
        } else {
          chatHistory.value.push({
            id: Date.now() + 3,
            text: response.data.message || '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ„ÇÇ„ÅÜ‰∏ÄÂ∫¶„ÅäË©¶„Åó„Åè„Å†„Åï„ÅÑ„ÄÇ',
            type: 'bot',
            isFavorite: false,
          });
        }
      } catch (error) {
        console.error('Error sending message:', error);
        const errorMessage = error.response && error.response.data && error.response.data.message
          ? error.response.data.message
          : '„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ„ÇÇ„ÅÜ‰∏ÄÂ∫¶„ÅäË©¶„Åó„Åè„Å†„Åï„ÅÑ„ÄÇ';
        chatHistory.value.push({
          id: Date.now() + 4,
          text: `„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${errorMessage}`,
          type: 'bot',
          isFavorite: false,
        });
      } finally {
        isLoading.value = false;
        scrollToBottom();
      }
    };

    /**
     * „É°„ÉÉ„Çª„Éº„Ç∏Â±•Ê≠¥„Çí„ÇØ„É™„Ç¢„Åô„ÇãÈñ¢Êï∞
     */
    const clearMessages = () => {
      chatHistory.value = [];
      // ÂøÖË¶Å„Å´Âøú„Åò„Å¶‰ªñ„ÅÆ„É™„Çª„ÉÉ„Éà„É≠„Ç∏„ÉÉ„ÇØ„ÇíËøΩÂä†
    };

    /**
     * „ÉÅ„É£„ÉÉ„ÉàÂ±•Ê≠¥„Çí‰∏ã„Å´„Çπ„ÇØ„É≠„Éº„É´„Åô„ÇãÈñ¢Êï∞
     */
    const scrollToBottom = () => {
      if (chatHistoryDiv.value) {
        chatHistoryDiv.value.scrollTop = chatHistoryDiv.value.scrollHeight;
      }
    };

    /**
     * „É≠„Ç∞„Ç§„É≥„Éö„Éº„Ç∏„Å´ÈÅ∑Áßª„Åô„ÇãÈñ¢Êï∞
     */
    const goToLogin = () => router.push('/login');

    /**
     * ‰ºöÂì°ÁôªÈå≤„Éö„Éº„Ç∏„Å´ÈÅ∑Áßª„Åô„ÇãÈñ¢Êï∞
     */
    const goToRegister = () => router.push('/register');

    /**
     * „Éû„Ç§„Éö„Éº„Ç∏„Å´ÈÅ∑Áßª„Åô„ÇãÈñ¢Êï∞
     */
    const goToMyPage = () => router.push('/mypage');

    /**
     * „É≠„Ç∞„Ç¢„Ç¶„Éà„Åô„ÇãÈñ¢Êï∞
     */
    const logout = async () => {
      try {
        const response = await axios.post('/logout', {}, { withCredentials: true });
        if (response.data.status === 'success') {
          isLoggedIn.value = false;
          toast.success('„É≠„Ç∞„Ç¢„Ç¶„Éà„Åó„Åæ„Åó„Åü„ÄÇ');
          clearMessages();
          router.push('/');
        } else {
          toast.error('„É≠„Ç∞„Ç¢„Ç¶„Éà„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
        }
      } catch (error) {
        console.error('Error logging out:', error);
        toast.error('„É≠„Ç∞„Ç¢„Ç¶„Éà‰∏≠„Å´„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ');
      }
    };

    onMounted(() => {
      checkLoginStatus();
    });

    return {
      message,
      chatHistory,
      isLoggedIn,
      isLoading,
      sendMessage,
      clearMessages,
      goToLogin,
      goToRegister,
      goToMyPage,
      logout,
      escapeHTML,
      chatHistoryDiv,
      toggleFavorite, // „ÅäÊ∞ó„Å´ÂÖ•„ÇäÈñ¢Êï∞„ÇíËøî„Åô
    };
  },
};
</script>

<style scoped>
