/* „ÅäÊ∞ó„Å´ÂÖ•„Çä„Éú„Çø„É≥„ÅÆ„Çπ„Çø„Ç§„É´ */
.favorite-button {
  position: absolute;
  top: 10px;
  right: -40px; /* „É°„ÉÉ„Çª„Éº„Ç∏„Éú„ÉÉ„ÇØ„Çπ„ÅÆÂè≥Â§ñÂÅ¥„Å´ÈÖçÁΩÆ */
