<template>
    <div class="register-container">
      <h2>‰ºöÂì°ÁôªÈå≤</h2>
      <form @submit.prevent="handleRegister">
        <div class="form-group">
          <label for="username">„É¶„Éº„Ç∂„ÉºÂêç</label>
          <input
            type="text"
            id="username"
            v-model="username"
            required
            placeholder="„É¶„Éº„Ç∂„ÉºÂêç„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div class="form-group">
          <label for="email">„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ</label>
          <input
            type="email"
            id="email"
            v-model="email"
            required
            placeholder="„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div class="form-group">
          <label for="password">„Éë„Çπ„ÉØ„Éº„Éâ</label>
          <input
            type="password"
            id="password"
            v-model="password"
            required
            placeholder="„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div class="form-group">
          <label for="confirmPassword">„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç</label>
          <input
            type="password"
            id="confirmPassword"
            v-model="confirmPassword"
            required
            placeholder="„Éë„Çπ„ÉØ„Éº„Éâ„ÇíÂÜçÂ∫¶ÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ"
          />
        </div>
        <div v-if="errorMessage" class="error-message">
          {{ errorMessage }}
        </div>
        <button type="submit" :disabled="isLoading">
          ‰ºöÂì°ÁôªÈå≤
        </button>
        <div v-if="isLoading" class="loading-spinner">
          ‰ºöÂì°ÁôªÈå≤‰∏≠...
        </div>
      </form>
      <p>
        „Åô„Åß„Å´„Ç¢„Ç´„Ç¶„É≥„Éà„Çí„ÅäÊåÅ„Å°„Åß„Åô„ÅãÔºü
        <router-link to="/login">„É≠„Ç∞„Ç§„É≥„ÅØ„Åì„Å°„Çâ</router-link>
      </p>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import { ref } from 'vue';
  import { useRouter } from 'vue-router';
  
  export default {
    name: 'Register',
    setup() {
      const username = ref('');
      const email = ref('');
      const password = ref('');
      const confirmPassword = ref('');
      const errorMessage = ref('');
      const isLoading = ref(false);
      const router = useRouter();
  
      const handleRegister = async () => {
        errorMessage.value = '';
  
        // „Éï„É≠„É≥„Éà„Ç®„É≥„Éâ„Åß„ÅÆ„Éë„Çπ„ÉØ„Éº„ÉâÁ¢∫Ë™ç
        if (password.value !== confirmPassword.value) {
          errorMessage.value = '„Éë„Çπ„ÉØ„Éº„Éâ„Åå‰∏ÄËá¥„Åó„Åæ„Åõ„Çì„ÄÇ';
          return;
        }
  
        isLoading.value = true;
  
        try {
          // ‰ºöÂì°ÁôªÈå≤„É™„ÇØ„Ç®„Çπ„Éà
          const registerResponse = await axios.post(
            'http://localhost:3000/api/register',
            {
              username: username.value,
              email: email.value,
              password: password.value,
            },
            {
              headers: {
                'Content-Type': 'application/json',
              },
              withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
            }
          );
  
          if (registerResponse.data.status === 'success') {
            // ‰ºöÂì°ÁôªÈå≤ÊàêÂäü
            // Ëá™Âãï„É≠„Ç∞„Ç§„É≥„É™„ÇØ„Ç®„Çπ„Éà
            const loginResponse = await axios.post(
              'http://localhost:3000/api/login',
              {
                username: username.value,
                password: password.value,
              },
              {
                headers: {
                  'Content-Type': 'application/json',
                },
                withCredentials: true, // „ÇØ„ÉÉ„Ç≠„Éº„ÇíÂê´„ÇÅ„Çã
              }
            );
  
            if (loginResponse.data.status === 'success') {
              // „É≠„Ç∞„Ç§„É≥ÊàêÂäü
              alert('‰ºöÂì°ÁôªÈå≤„Å®„É≠„Ç∞„Ç§„É≥„Å´ÊàêÂäü„Åó„Åæ„Åó„Åü„ÄÇ„Éõ„Éº„É†„Éö„Éº„Ç∏„Å´ÁßªÂãï„Åó„Åæ„Åô„ÄÇ');
              router.push('/'); // „Éõ„Éº„É†„Éö„Éº„Ç∏„Å´„É™„ÉÄ„Ç§„É¨„ÇØ„Éà
            } else {
              // „É≠„Ç∞„Ç§„É≥Â§±ÊïóÔºàÁ®ÄÔºâ
              errorMessage.value = loginResponse.data.message || '„É≠„Ç∞„Ç§„É≥„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
            }
          } else {
            // ‰ºöÂì°ÁôªÈå≤Â§±Êïó
            errorMessage.value = registerResponse.data.message || '‰ºöÂì°ÁôªÈå≤„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ';
          }
        } catch (error) {
          console.error('‰ºöÂì°ÁôªÈå≤„Ç®„É©„Éº:', error);
          errorMessage.value = error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ';
        } finally {
          isLoading.value = false;
        }
      };
  
      return {
        username,
        email,
        password,
        confirmPassword,
        handleRegister,
        errorMessage,
        isLoading,
      };
    },
  };
  </script>
  
  <style scoped>
  .register-container {
