<template>
  <div class="favorites">
    <header>
      <h1>„ÅäÊ∞ó„Å´ÂÖ•„Çä‰∏ÄË¶ß</h1>
      <button @click="goBack">Êàª„Çã</button>
    </header>
    <div class="favorites-container">
      <div v-if="isLoading" class="loading-indicator">
        <div class="spinner"></div>
        <span>„ÅäÊ∞ó„Å´ÂÖ•„Çä„Éá„Éº„Çø„ÇíË™≠„ÅøËæº„Åø‰∏≠...</span>
      </div>
      
      <div v-if="!isLoading && favorites.length === 0" class="no-favorites">
        „ÅäÊ∞ó„Å´ÂÖ•„Çä„Å´ÁôªÈå≤„Åï„Çå„Åü„Ç§„É≥„Çø„É©„ÇØ„Ç∑„Éß„É≥„Åå„ÅÇ„Çä„Åæ„Åõ„Çì„ÄÇ
      </div>
      
      <div v-else class="favorites-list">
        <div v-for="item in favorites" :key="item.id" class="favorite-card">
          <h3>ÂØæË©± #{{ item.id }}</h3>
          <p><strong>„É¶„Éº„Ç∂„Éº:</strong> {{ item.user_message }}</p>
          <p><strong>AI:</strong> {{ item.ai_message }}</p>
          <button
            :class="['btn', 'btn-danger']"
            @click="removeFavorite(item.id)"
            aria-label="„ÅäÊ∞ó„Å´ÂÖ•„Çä„ÇíËß£Èô§„Åô„Çã"
          >
            „ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from '@/axios'; // Ê®ôÊ∫ñ„ÅÆaxios„Çí‰ΩøÁî®
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { useToast } from 'vue-toastification';

export default {
  name: 'Favorites',
  setup() {
    const favorites = ref([]);
    const isLoading = ref(false);
    const router = useRouter();
    const toast = useToast();

    /**
     * „É¶„Éº„Ç∂„Éº„ÅÆ„ÅäÊ∞ó„Å´ÂÖ•„Çä„Ç¢„Ç§„ÉÜ„É†„ÇíÂèñÂæó„Åô„ÇãÈñ¢Êï∞
     */
    const fetchFavorites = async () => {
      isLoading.value = true;
      try {
        const response = await axios.get('/favorites', { withCredentials: true });
        if (response.data.status === 'success') {
          favorites.value = response.data.favorites;
        } else {
          toast.error(response.data.message || '„ÅäÊ∞ó„Å´ÂÖ•„Çä„ÅÆÂèñÂæó„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
        }
      } catch (error) {
        console.error('„ÅäÊ∞ó„Å´ÂÖ•„ÇäÂèñÂæó„Ç®„É©„Éº:', error);
        toast.error(error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ');
      } finally {
        isLoading.value = false;
      }
    };

    /**
     * „ÅäÊ∞ó„Å´ÂÖ•„Çä„ÇíËß£Èô§„Åô„ÇãÈñ¢Êï∞
     * @param {Number} messageId 
     */
    const removeFavorite = async (messageId) => {
      try {
        const response = await axios.delete(`/favorites/${messageId}`, { withCredentials: true });
        if (response.data.status === 'success') {
          favorites.value = favorites.value.filter(item => item.id !== messageId);
          toast.success('„ÅäÊ∞ó„Å´ÂÖ•„Çä„Åã„ÇâËß£Èô§„Åó„Åæ„Åó„Åü„ÄÇ');
        } else {
          toast.error(response.data.message || '„ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§„Å´Â§±Êïó„Åó„Åæ„Åó„Åü„ÄÇ');
        }
      } catch (error) {
        console.error('„ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§„Ç®„É©„Éº:', error);
        toast.error(error.response?.data?.message || '„Çµ„Éº„Éê„Éº„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü„ÄÇ');
      }
    };

    /**
     * Êàª„Çã„Éú„Çø„É≥„ÅÆÊ©üËÉΩ
     */
    const goBack = () => {
      router.push('/'); // „Éõ„Éº„É†ÁîªÈù¢„Å´Êàª„Çã
    };

    onMounted(() => {
      fetchFavorites();
    });

    return {
      favorites,
      isLoading,
      removeFavorite,
      goBack,
    };
  },
};
</script>

<style scoped>
