  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  font-size: 16px;
}

header button:hover {
  background-color: #0056b3;
}

.favorites-container {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 20px;
  background-color: #f9f9f9;
  position: relative;
}

.loading-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
}

.spinner {
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 24px;
  height: 24px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.no-favorites {
  text-align: center;
  font-size: 18px;
  color: #555;
}

.favorites-list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.favorite-card {
  width: 300px;
  padding: 20px;
  border: 1px solid #bbb;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 2px 2px 12px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.favorite-card h3 {
  margin-bottom: 10px;
}

.favorite-card p {
  margin-bottom: 10px;
}

/* „Éú„Çø„É≥„ÅÆ„Çπ„Çø„Ç§„É´ */
.btn {
  width: 100%;
  padding: 12px;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin: 10px 0; /* ‰∏ä‰∏ã„ÅÆ‰ΩôÁôΩ„ÇíÁµ±‰∏Ä */
  transition: background-color 0.3s ease;
}

.btn-danger {
  background-color: #dc3545; /* „ÅäÊ∞ó„Å´ÂÖ•„ÇäËß£Èô§„Éú„Çø„É≥„ÅÆËâ≤ */
}

.btn-danger:hover {
  background-color: #c82333;
}

/* „É¨„Çπ„Éù„É≥„Ç∑„Éñ„Éá„Ç∂„Ç§„É≥ */
@media (max-width: 600px) {
  .favorites-list {
    flex-direction: column;
    align-items: center;
  }

